<template>
	<div class="container">
		<div class="row">
			<div class="col-md-6 resumeStyle" ref="resumeStyle">
				<div class="col-xs-12 noScroll">					
					<pre ref="resumePre">{{preMsg}}</pre>
				</div>
			</div>
			<div class="col-md-6 resumeContain" ref="resumeContain">
				<div class="col-xs-12 noScroll">
					<div ref="resumeContainRight" class="resumeContainRight">
						<p class="name"></p>
						<div class="row">
							<div class="col-md-3">
								<p> | 1992/02/15</p>
							</div>
							<div class="col-md-9">
								<p>: </p>
							</div>
						</div>
						<div class="row">
							<div class="col-md-3">
								<p>: </p>
							</div>
							<div class="col-md-9">
								<p>: </p>
							</div>
						</div>
						<div class="row">
							<div class="col-md-3">
								<p>: </p>
							</div>
							<div class="col-md-9">
								<p>/: /</p>
							</div>
						</div>
						<p>: </p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Hello World</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code>html</code><code>css</code><code>javascript</code></p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;201511offer</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code>JqueryAjaxHTML5CSS3Bootstrap</code>Jquery<code>AngularJs</code></p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;20166Vue.jsAngularJsVue.jsVue.js</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code>Vue.js</code>Vue.jsUI<code>VuexVUXElementUIiViewMint UI</code></p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code>ES6</code>SetMapPromise</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code>SVNgitEchartHighchartsAPIAPISEO</code></p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code>node.jsExpressMongoDB</code>JavaScriptJavaScriptJavaScript<code>http://www.cnblogs.com/zlfProgrammer/</code></p>
						<p>: </p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p>
						<p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<code></code></p>
						<p></p>
						<p class="linkContain">
							<span class="qqContain">							
								<a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=412606846&site=qq&menu=yes"><img border="0" src="http://wpa.qq.com/pa?p=2:2047599731:41" alt="" title="" /></a>
								<div class="qq-popover">
									<img src="./../../static/qq.png">
								</div>
							</span>
							
							<span class="wechatContain">
								<img class="wechat" src="./../../static/wechat.png" />
								<div class="sns-popover">
									<img src="./../../static/wechat.jpg">
								</div>
							</span>

							<span class="githua">
								<a href="https://github.com/zhuDadaa" target='_blank'>
									<img class="wechat" src="./../../static/GitHub.png" width="25"/>
								</a>
							</span>
						</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
    	windowHeight: $(window).height()-20,
    	preMsg: ""
    }
  },
  created: function() {
		this.$nextTick(() => {
  			this.setContentHeight();
		});
  },
  methods: {
  	setContentHeight: function(){
  		$(this.$refs.resumeStyle).height( $(window).height()-65 );
  		$(this.$refs.resumePre).height( $(window).height()-65 );
  		$(this.$refs.resumeContain).height( $(window).height()-65 );
  		$(this.$refs.resumeContainRight).height( $(window).height()-65 );
  	},
  	setChangeContentHeight: function(){
  		var _this = this;
  		window.onresize= function(){
		  	_this.$nextTick(() => {
		  		_this.setContentHeight();
			});
	  	};
  	},
  	resumeShow: function(){
  		var str = `/*
* 
* ,
* 
* ,,
* 
* ,
* */
/**
 * ,
 */
*{
 transition: all 0.5s ease-in;
 margin: 0;
 padding: 0;
}
/**
 * 
 */
#particles-js{
 opacity: .8;
 background: -webkit-gradient(linear,left top,right bottom,from(#50a3a2),to(#53e3a6));
 background: linear-gradient(to bottom right,#50a3a2,#53e3a6);
 background: -webkit-linear-gradient(to bottom right,#50a3a2,#53e3a6);
}
/**
 * 
 * 
 */
.resumeStyle{
 background-color: rgba(85,163,184,0.3);
}
.resumeStyle .noScroll pre{
 color: white;
}
/**
 * 
 * ,~
 */
.resumeStyle{
 transform: perspective(1200px) rotateY(20deg) rotateX(5deg);
 -ms-transform: perspective(1200px) rotateY(20deg) rotateX(5deg);
 -moz-transform: perspective(1200px) rotateY(20deg) rotateX(5deg);
 -webkit-transform: perspective(1200px) rotateY(20deg) rotateX(5deg);
 -o-transform: perspective(1200px) rotateY(20deg) rotateX(5deg);
 border-radius: 4px;
 box-shadow: 0px 4px 3px rgba(0,0,0,0.3);
}
/**
 * ,
 */
.resumeContain{
 background-color: rgba(85,163,184,0.3);
 color: white;
 border-radius: 4px;
 box-shadow: 0px 4px 3px rgba(0,0,0,0.3);
 transform: perspective(1200px) rotateY(-20deg) rotateX(5deg);
 -ms-transform: perspective(1200px) rotateY(-20deg) rotateX(5deg);
 -moz-transform: perspective(1200px) rotateY(-20deg) rotateX(5deg);
 -webkit-transform: perspective(1200px) rotateY(-20deg) rotateX(5deg);
 -o-transform: perspective(1200px) rotateY(-20deg) rotateX(5deg);
}






`;
		this.typing(str);
	},
	typing: function(str){
		var _this = this;
		var i=0;
		var typings = function(){
			if (i <= str.length) {
		    	_this.preMsg = str.slice(0, i++) + '_\n\n\n';
		    	if(str[i] == "}"){
		    		$("#styleObject").html("");
		    		$("#styleObject").append(str.slice(0, i++) + "}");
		    	}
		    	$(".resumeStyle pre").scrollTop($(".resumeStyle pre")[0].scrollHeight);
		    	setTimeout(function(){
		    		typings();
		    	}, 20);//
		   	}else{
		    	_this.preMsg = str;//, _ 
		    	setTimeout(function(){		    		
		    		$(".resumeContainRight").fadeIn();
		    	},1000);
		    }
		};
		typings();
	}
  },
  beforeMount: function(){
  	this.setChangeContentHeight();
  },
  mounted: function() {
  	this.resumeShow();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.resumeStyle,.resumeContain{
		margin-top: 30px;
		padding: 0;
	}
	.noScroll{
		width: 100%;
		overflow: hidden;
	}
	.resumeStyle .noScroll pre{
		margin: 0;
		margin-bottom: -20px;
		margin-right: -32px;
		padding: 0;
		padding-right: 10px;
		border: 0px;
	}
	.table{
		margin-bottom: 0px;
	}
	.name{
		font-size: 24px;
	}
	.resumeContainRight{
		overflow: auto;
		margin: 0;
		margin-bottom: -20px;
		margin-right: -32px;
		padding: 0;
		border: 0px;
	}
	.resumeContainRight p{
		margin-right: 10px;
	}
	.linkContain{
		margin-bottom: 20px;
		text-align: center;
	}
	.wechat{
		margin-left: 10px;
	}
	.wechatContain,.qqContain{
		position: relative;
    	display: inline-block;
	}
	.qq-popover img, .sns-popover img {
		width: 175px;
	}
	.wechatContain:hover .sns-popover {
		-webkit-transform: scale(1, 1);
		-moz-transform: scale(1, 1);
		-ms-transform: scale(1, 1);
		-o-transform: scale(1, 1);
		transform: scale(1, 1);
		opacity: 1;
		filter: alpha(opacity=100);
		visibility: visible;
	}
	.qqContain:hover .qq-popover {
		-webkit-transform: scale(1, 1);
		-moz-transform: scale(1, 1);
		-ms-transform: scale(1, 1);
		-o-transform: scale(1, 1);
		transform: scale(1, 1);
		opacity: 1;
		filter: alpha(opacity=100);
		visibility: visible;
	}
	.sns-popover,.qq-popover {
		width: 194px;
		height: 209px;
		position: absolute;
		left: -79px;
		top: -200px;
		visibility: hidden;
		opacity: 0;
		filter: alpha(opacity=0);
		-webkit-transform: scale(0, 0);
		-moz-transform: scale(0, 0);
		-ms-transform: scale(0, 0);
		-o-transform: scale(0, 0);
		transform: scale(0, 0);
		-webkit-transition: all .3s ease;
		-moz-transition: all .3s ease;
		-o-transition: all .3s ease;
		transition: all .3s ease;
		transform-origin: 50% 100%;
		-webkit-transform-origin: 50% 100%;
		-moz-transform-origin: 50% 100%;
		-o-transform-origin: 50% 100%;
	}
	.resumeContainRight{
		display: none;
	}
</style>



