<template>
	<div id="app">
		<vue-particles
	        color="#ffffff"
	        :particleOpacity="0.3"
	        :particlesNumber="120"
	        shapeType="star"
	        :particleSize="4"
	        linesColor="#ffffff"
	        :linesWidth="1"
	        :lineLinked="true"
	        :lineOpacity="0.4"
	        :linesDistance="150"
	        :moveSpeed="3"
	        :hoverEffect="true"
	        hoverMode="grab"
	        :clickEffect="true"
	        clickMode="push">
	    </vue-particles>
	    <!--"vue-particles": "^1.0.9",
	    color: String'#dedede'
		particleOpacity: Number0.7
		particlesNumber: Number80
		shapeType: String'circle'"circle","edge","triangle", "polygon","star"
		particleSize: Number80
		linesColor: String'#dedede'
		linesWidth: Number1
		lineLinked: true
		lineOpacity: Number0.4
		linesDistance: Number150
		moveSpeed: Number3
		hoverEffect: truehover
		hoverMode: Stringtruehover: "grab", "repulse", "bubble"
		clickEffect: trueclick
		clickMode: Stringtrueclick: "push", "remove", "repulse", "bubble"-->
	    <HelloWorld/>
	</div>
</template>

<script>
	import HelloWorld from './components/HelloWorld'

	export default {
		name: 'App',
		components: {
			HelloWorld
		},
	}
</script>

<style>
	body{
		overflow-x: hidden;
	}
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
	.resumeStyle .noScroll pre {
		background-color: rgba(255,255,255,0);
	}
	#particles-js{
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: cover;
      background-position: 50% 50%;
	}
</style>


